import { useContext } from "react";
import { AuthenContext } from "../context/AuthenContext";
import { Button, Stack } from "@mui/material";
import { useNavigate } from "react-router-dom";

export default function MainMenu() {
    let { logout } = useContext(AuthenContext);
    let navigate = useNavigate()

    return <Stack spacing={2} >
        <Button variant="text" onClick={() => navigate("/dashboard/category/new")}>Add Category</Button>
        <Button variant="text" onClick={() => navigate("/dashboard/categories")}>Categories</Button>
        <Button variant="text" onClick={logout}>Logout</Button>
    </Stack>
}