import { createSlice } from '@reduxjs/toolkit'
import { findCategoriesAPI, getCategoryByIdAPI } from '../service/category.service'

const initialState = {
    recordsTotal: 0,
    recordsFiltered: 0,
    categories: [],
    category: null,
    isLoading: false,
    error: null,
    search: {
        start: 0,
        length: 10,
        keyword: ''
    }
}

export const categorySlice = createSlice({
    name: 'category',
    initialState,
    reducers: {
        startLoading: (state) => {
            state.isLoading = true
        },
        setError: (state, action) => {
            state.isLoading = false
            state.error = action.payload
        },
        setCategories: (state, action) => {
            // map voi response tr v t api
            state.categories = action.payload.data
            state.recordsFiltered = action.payload.recordsFiltered
            state.recordsTotal = action.payload.recordsTotal
            state.isLoading = false
            state.error = null
        },
        setCategory: (state, action) => {
            // map voi response tr v t api theo id, 1 object
            state.category = action.payload.data
            state.isLoading = false
            state.error = null
        },
        setCategorySearch: (state, action) => {
            state.search = action.payload
        },
    },
})

// Action creators are generated for each case reducer function
export const { setCategorySearch } = categorySlice.actions

//redux thunk, l hm tr v 1 hm bn trong cha 2 tham s dispatch  gi khi c d liu t api tr v, getstate c d liu sn t store ra.
export const searchCategory = () =>
    async (dispatch, getState) => {
        dispatch(categorySlice.actions.startLoading())

        const { category } = getState() // doc tu store cua redux
        const { search } = category

        const { code, result } = await findCategoriesAPI(search)

        if (code === 200)
            dispatch(categorySlice.actions.setCategories(result))
        else
            dispatch(categorySlice.actions.setError(code))
    }

export const getCategoryById = (id) =>
    async (dispatch) => {
        categorySlice.actions.startLoading()

        const { code, result } = await getCategoryByIdAPI(id)

        if (code === 200)
            dispatch(categorySlice.actions.setCategory(result))
        else
            dispatch(categorySlice.actions.setError(code))
    }

export default categorySlice.reducer