import { Avatar, Container, Grid, LinearProgress, Stack, Typography } from '@mui/material';
import { Provider } from 'react-redux';
import { BrowserRouter, Navigate, Outlet, Route, Routes } from 'react-router-dom';
import { ToastContainer } from 'react-toastify';
import './App.css';
import Footer from './components/Footer';
import Header from './components/Header';
import MainMenu from './components/MainMenu';
import { AuthenProvider } from './context/AuthenContext';
import { useAuth } from './hooks/useAuth';
import logo from './logo.svg';
import { EditCategory } from './pages/category/EditCategory';
import NewCategory from './pages/category/NewCategory';
import SearchCategory from './pages/category/SearchCategory';
import { LoginForm } from './pages/LoginForm';
import { store } from './redux/store';

function App() {
  return (
    <Provider store={store}>
      <AuthenProvider>
        <MainApp />
        <ToastContainer />
      </AuthenProvider>
    </Provider>
  );
}

function MainApp() {
  return <BrowserRouter basename='/public'>
    <Routes>
      <Route path="/login" element={<LoginTemplate />}>
        <Route index element={<LoginForm />} />
      </Route>

      <Route path="/dashboard" element={<DashboardTemplate />}>
        <Route index element={<Navigate to={"/dashboard/categories"} />} />

        <Route path="category/new" element={<NewCategory />} />
        <Route path="category/:id/edit" element={<EditCategory />} />
        <Route path="categories" element={<SearchCategory />} />
      </Route>

      <Route path="/" element={<Navigate to={"/dashboard"} />} />
      <Route path="*" element={<NotFound />} />
    </Routes>
  </BrowserRouter >;
}

function LoginTemplate() {
  let { isAuthenticated, isLoading } = useAuth();

  if (isLoading)
    return <LinearProgress />;

  if (isAuthenticated)
    return <Navigate to={"/dashboard"} />;

  return (<>
    <Header />
    <Outlet />
  </>);
}

function DashboardTemplate() {
  let { isAuthenticated, isLoading } = useAuth();

  if (isLoading)
    return <LinearProgress />;

  if (isAuthenticated)
    return (
      <Grid container spacing={2}>
        <Grid item xs={12}>
          <Header />
        </Grid>
        <Grid item md={2} xs={12}>
          <MainMenu />
        </Grid>
        <Grid item xs={12} md={10}>
          <Outlet />
        </Grid>
        <Grid item xs={12}>
          <Footer />
        </Grid>
      </Grid>
    );
  // JSX
  return <Navigate to={"/login"} />;
}

function NotFound() {
  return (<Container maxWidth="md">
    <Grid container spacing={2}>
      <Grid item xs={12} >
        <Stack spacing={2} alignItems="center"
          justifyContent="center">
          <Avatar src={logo} variant="square" sx={{ width: 240, height: 240 }} />
          <Typography variant='h1'>404 Not Found</Typography>
        </Stack>
      </Grid>
    </Grid>
  </Container>);
}

export default App;
